import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
var flag=true;

class Bottom extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
            <TabBar
                unselectedTintColor="#949494"
                tintColor="red"
                barTintColor="white"
            >
            <TabBar.Item
                title="店铺"
                key="home"
                icon={
                    <i className='iconfont icon-dianpu'></i>                           
                }
                selectedIcon={
                    <i className='iconfont icon-dianpu'></i>                           
                }
                selected={ pathname === '/' }
                onPress={ () => {
                     this.props.history.push('/');
                } }
                data-seed="logId"
            >
            </TabBar.Item>
            <TabBar.Item
                icon={
                    <i className='iconfont icon-kefu'></i>                           
                }
                selectedIcon={
                    <i className='iconfont icon-kefu'></i>                           
                }
                title="客服"
                key="cart"
                selected={ pathname === '/cart' }
                onPress={ () => {
                    this.props.history.push('/cart')
                } }
                data-seed="logId1"
            ></TabBar.Item>
            <TabBar.Item
                icon={
                    <i className='iconfont icon-shoucang' id='empty' style={{opacity:"1"}}></i>                           
                }
                icon1={
                    <i className='iconfont icon-shoucang' id='full' style={{opacity:"0"}}></i>
                }
                selectedIcon={
                    <div style={ {
                        width: '22px',
                        height: '22px',
                        background: 'url(https://zos.alipayobjects.com/rmsportal/IIRLrXXrFAhXVdhMWgUI.svg) center center /  21px 21px no-repeat'
                    } }
                    />
                }
                title="收藏"
                key="Friend"
                selected={ this.state.selectedTab === 'greenTab' }
                onPress={ () => {
                    if(flag){
                        var empty = document.getElementById('empty').opacity;
                        empty="0";
                        // document.getElementById('full').style.opacity="1";
                        flag=!flag
                    }
                    // this.props.history.push('/Enter')
                } }
               
            >
             
            </TabBar.Item>
            <TabBar.Item
                icon={<button style={{height:'35px',width:'100px',borderTopLeftRadius:'20px',borderTopRightRadius:'0px',borderBottomLeftRadius:'20px',borderBottomRightRadius:'0px',backgroundColor:"orange",color:'white'}}>加入购物车</button> }
                key="madd to cart"
                onPress={ () => {
                    window.location='http://www.alipay.com'
                } }
            >
			</TabBar.Item>
            <TabBar.Item
                icon={<button style={{height:'35px',width:'100px',borderTopLeftRadius:'0px',borderTopRightRadius:'20px',borderBottomLeftRadius:'0px',borderBottomRightRadius:'20px',backgroundColor:"red",color:'white'}}>立即购买</button> }
                key="take it now"
                onPress={ () => {
                    window.location='http://www.alipay.com'
                } }
            >
            </TabBar.Item>
            </TabBar>
            </div>
        );
    }
}
export default withRouter(Bottom);