import React from 'react'
import { SearchBar, Button, WhiteSpace, WingBlank } from 'antd-mobile';
import {withRouter} from 'react-router-dom'

const S1=(props)=> {
    return (
        <div>
            <SearchBar
                placeholder="搜索宝贝"
                onCancel={
                    ()=>{
                        props.history.goBack();
                    }
                }
                showCancelButton
                //onChange={this.onChange}
            />
            <div>
                <p>您好，页面已跳转</p>
            </div>
        </div>
    )
}

export default withRouter(S1);
