import React from 'react'
import FlexExample from './Picture'
import{withRouter} from 'react-router-dom'
import Bottom from './Bottom'

class S2 extends React.Component{
    constructor(props){
        super(props);
    }
    render(){
        return (
            <div>
                <div>
                <p
                    onClick={
                        ()=>{
                            this.props.history.push('/');
                        }
                    }
                >{`<=`}</p>
              
                <img 
                    src='https://gw.alicdn.com/bao/uploaded/i1/TB19I8UHVXXXXcXXFXXXXXXXXXX_!!0-item_pic.jpg_500x500q90.jpg_.webp'
                    style={{width:'100%'}}
                ></img>
                </div>
                <div>
                 <Bottom />
                </div>
            </div>
        )
    }
}

export default withRouter(S2);
