import React from 'react'
import {withRouter } from 'react-router-dom';
import {WingBlank ,SearchBar , Button , withSpace , Flex , Carousel } from 'antd-mobile';
class Search extends React.Component{
    onChange=(value)=>{
        this.setState({value});
    };
    clear=()=>{
        this.setState({value:''});
    };
    render(props){
        return(
            <div>
                <withSpace />
                <WingBlank><div className="sub-title"></div></WingBlank>
                <SearchBar 
                placeholder="搜索宝贝"
                maxLength={200}
                />
            </div>
        );
    }
}
export default withRouter(Search);