import React from 'react'
import {withRouter } from 'react-router-dom';
import {WingBlank ,SearchBar , Button , withSpace , Flex , Carousel } from 'antd-mobile';
class Firstpage extends React.Component{
    state = {
        data:['1','2','3'],
        imageHeight:200,
    }
    componentDidMount(){
        setTimeout(()=>{
            this.setState({
                data:['https://gw.alicdn.com/imgextra/i3/141/O1CN01sz1ZAN1CucTfxhdPX_!!141-0-ppp-picassogw.jpg'],
            });
        },100)
    }
    onChange = (value) => {
        this.setState({value});
    };
    clear =(value) =>{
        this.setState({value:""})
    };
    render(props){
        return(
            <div>
                <withSpace />
                <WingBlank><div className="taobao"></div></WingBlank>
                <SearchBar placeholder='搜索宝贝' maxLength={180}/>
                <WingBlank>
                    <Carousel autoplay={true} infinite beforeChange={(from,to)=>console.log(`slide from ${from} to ${to}`)} afterChange={index=>console.log('slide to',index)}>
                        {this.state.data.map((item,index)=>(
                            <a key={index} href='http://www.alipay.com' style={{display:'inline-block',width:'100%',height:this.state.imageHeight}}>
                                <img src={`${item}`} alt='' style={{width:'100%',verticalAlign:'top'}}
                                onLoad={()=>{
                                    window.dispatchEvent(new Event('resize'));
                                    this.setState({
                                        imageHeight:'auto'
                                    });
                                }}/>
                            </a>
                        ))}
                    </Carousel>
                </WingBlank>
                <div className='taobao'>你可能喜欢</div>
                <Flex>
                    <Flex.Item><img src='https://gw.alicdn.com/bao/uploaded/i1/735011836/O1CN01YuXrtk1PQvm3Lph0k_!!735011836-0-lubanu-s.jpg_500x500q90.jpg_.webp' onClick={()=>{this.props.history.push('./p')}} style={{width:"75%"}}/><placeholder /></Flex.Item>
                    <Flex.Item><img src='https://gw.alicdn.com/bao/uploaded/i2/649769037/O1CN01SjbMva2GczlS04MJt_!!649769037.jpg_500x500q90.jpg_.webp' onClick={()=>{this.props.history.push('./p')}} style={{width:"75%"}}/><placeholder /></Flex.Item>
                    <Flex.Item><img src='https://gw.alicdn.com/bao/uploaded/i1/23187583/TB2CR5aqgxlpuFjy0FoXXa.lXXa_!!23187583.jpg_500x500q90.jpg_.webp' onClick={()=>{this.props.history.push('./p')}} style={{width:"75%"}}/><placeholder /></Flex.Item>
                    <Flex.Item><img src='https://gw.alicdn.com/bao/uploaded/i1/466362427/O1CN01KzCxFH1TnbrphQzsI_!!0-item_pic.jpg_500x500q90.jpg_.webp' onClick={()=>{this.props.history.push('./p')}} style={{width:"75%"}}/><placeholder /></Flex.Item>
                    <Flex.Item><img src='https://gw.alicdn.com/bao/uploaded/i1/1748559219/O1CN01Sgqr802HyLtxB6jVH_!!0-item_pic.jpg_500x500q90.jpg_.webp' onClick={()=>{this.props.history.push('./p')}} style={{width:"75%"}}/><placeholder /></Flex.Item>
                    <Flex.Item><img src='https://gw.alicdn.com/bao/uploaded/i1/413996455/O1CN01aCsWWf1xYRBh2fmmz_!!413996455.jpg_500x500q90.jpg_.webp' onClick={()=>{this.props.history.push('./p')}} style={{width:"75%"}}/><placeholder /></Flex.Item>
                    <Flex.Item><img src='https://gw.alicdn.com/bao/uploaded/i4/TB1UoBOKpXXXXX5XpXXXXXXXXXX_!!0-item_pic.jpg_500x500q90.jpg_.webp' onClick={()=>{this.props.history.push('./p')}} style={{width:"75%"}}/><placeholder /></Flex.Item>
                    <Flex.Item><img src='https://gw.alicdn.com/bao/uploaded/i3/643292436/TB24riEblRRMKJjSZPhXXcZoVXa_!!643292436.jpg_500x500q90.jpg_.webp' onClick={()=>{this.props.history.push('./p')}} style={{width:"75%"}}/><placeholder /></Flex.Item>
                    <Flex.Item><img src='https://gw.alicdn.com/bao/uploaded/i2/1990999383/O1CN01VD0yZH2JBSn6yl8EM_!!1990999383.jpg_500x500q90.jpg_.webp' onClick={()=>{this.props.history.push('./p')}} style={{width:"75%"}}/><placeholder /></Flex.Item>
                    <Flex.Item><img src='https://gw.alicdn.com/bao/uploaded/i4/860493484/O1CN01WsZIfk1bbiV7t8c1p_!!0-item_pic.jpg_500x500q90.jpg_.webp' onClick={()=>{this.props.history.push('./p')}} style={{width:"75%"}}/><placeholder /></Flex.Item>
                </Flex>
            </div>
        );
    }
}
export default Firstpage;